import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { GlobalService } from 'src/app/service/global.service';
import { environment } from 'src/environments/environment';
import { AuthService } from 'src/app/service/auth.service';
import { Title, Meta } from '@angular/platform-browser';

@Component({
    selector: 'app-home',
    templateUrl: './home.component.html',
    styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {


    allCategory: any = [];
    assetsURL = environment.assetsURL;
    widthHeightSizeInPixels: any = 173;

    constructor(private titleService: Title,
        private meta: Meta, private auth_service: AuthService,
        public gs: GlobalService,

        private router: Router) {

        if (localStorage.getItem('buyerData') != null) {
            this.gs.loginData = JSON.parse(localStorage.getItem('buyerData') || '')
        }
        this.gs.updatePageName('Finely Curated Artisanal and Lifestyle Products for Wholesale Buying | ExpoBazaar');

    }

    ngOnInit(): void {
        this.getAllCate();
        this.gs.loadScript();
        this.meta.addTag({ name: 'robots', content: 'index, follow' });
        // this.meta.addTag({ name: 'keywords', content: 'Browse Job-Contractual Jobs in India, Contract Jobs, Flexible Period Job, Contract Job India, Contract job Vacancy, Jobs in India, Jobs in Jaipur, Jobs in Mumbai, Jobs in Chandigarh, IT jobs in India, Developer Jobs in India, Manager jobs in India, highest paying jobs in India, job portals in India, Jobs Near Me' });
        this.meta.addTag({
            name: 'description',
            content:
                'This is expobazaar Home Page.',
        });
        this.meta.addTag({ name: 'og:title', content: 'This is expobazaar Title' });
        this.meta.addTag({
            name: 'og:description',
            content: 'This is expobazaar description',
        });
    }

    getAllCate() {
        this.auth_service.getAllParentCategory().subscribe((resp: any) => {
            if (resp.status) {
                this.allCategory = resp.data;
            }
        }, (error: any) => {
            console.log(error);
        });
    }

    shop(id: any) {
        this.router.navigate(['/home/shop', id]);
        // if (this.gs.loginData && this.gs.loginData['_id']) {
        // } else {
        //     this.router.navigate(['/signin'], { queryParams: { catId: id } });
        // }
    }
}

