import { Component, OnInit } from '@angular/core';
import { GlobalService } from 'src/app/service/global.service';
import { ActivatedRoute, Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { environment } from 'src/environments/environment';
import { SettingService } from 'src/app/service/setting.service';
import { CommonService } from 'src/app/service/common.service';
declare const $: any;

@Component({
    selector: 'app-search-result',
    templateUrl: './search-result.component.html',
    styleUrls: ['./search-result.component.scss']
})
export class SearchResultComponent implements OnInit {
    assetsURL = environment.assetsURL;
    isSpinner: boolean = false;
    filterKey: any = '';
    productList: any = [];
    childCategory: any = [];
    brandList: any = [];
    colorsList: any = [];
    sellerList: any = [];
    readonly = true;
    breadcrumb: any = [{ name: 'Home' }];
    searchData: any = {};
    isLoading: boolean = false;
    serachText: any;
    category_id: any;
    tmpData: any = [];
    productDetails: any = {}
    wishlistItems: any = [];
    countryList: any = [];
    wishlistId: any = '';
    productData: any = {};
    isReadMore = true;
    filterPrice: any = {};
    actionPermissions: any = {};
    buyerId: any;
    p: any = 0;

    formObj: any = {
        user_collection: {
            user_id: this.gs.loginData['_id'] || '',
            collection_name: 'cart',
            collection_type: '',
            payment_card_id: '1',
            ship_from: '',
            terms_agree: '',
            status_reason: '',
            delivery_charges: '',
            taxes: '',
            related_business_indv_id: '',
            order_id: null,
            user_collection_seller: [],
            status: 'active',
            editedby: null,
            deleted: null,
            address_id: '',
            create_user: this.gs.loginData['_id'] || '',
            deletedby: null
        },

        collection_product: {
            user_collection_id: '',
            product_id: '',
            item_price: {},
            quantity: 1,
            status: 'active',
            editedby: null,
            deleted: null,
            create_user: this.gs.loginData['_id'] || '',
            deletedby: null
        }
    }

    filterObj: any = {
        catId: '',
        brand: '',
        color: '',
        seller: ''
    }

    constructor(
        public gs: GlobalService,
        private toastr: ToastrService,
        private routea: ActivatedRoute,
        private router: Router,
        public setService: SettingService,
        public common: CommonService
    ) {
        this.gs.updatePageName('Search');

        if (this.gs.loginData && this.gs.loginData['_id']) {
            this.setService.getPermission({ id: this.gs.loginData['_id'] }).subscribe((resp: any) => {
                this.actionPermissions = resp['data'];
            }, (error: any) => {
                console.log(error);
            });

            this.routea.queryParamMap.subscribe((params) => {
                this.buyerId = params.get('bcid');
            });
        }

        // this.serachText = this.routea.snapshot.queryParamMap.get('text');
        // this.category_id = this.routea.snapshot.queryParamMap.get('catid');
        this.routea.queryParams.subscribe(p => {
            this.serachText = p['text']
            this.category_id = p['catid']
            this.getSearchData();
            if (p && p.text) {
                this.searchData = p.text;
            }
        });
        // this.getSearchData();
    }

    ngOnInit(): void {
        this.setService.getCodeDomainVal('SHIPMODEL').subscribe((resp: any) => {
            if (resp['status']) {
                this.countryList = resp['data'];
            }
        });

        this.gs.loadScript();
        setTimeout(() => {
            this.priceRange();
        }, 300)
    }

    ngOnDestroy() {
        this.gs.searchTxt = '';
    }

    showText() {
        this.isReadMore = !this.isReadMore
    }

    // sidebarFilter(item,key) {
    //     if (key == 1) {
    //         this.router.navigate(['/shop', item._id]);
    //     }
    // }

    //View product Detail Modal
    viewProductDetail(row: any) {
        this.productDetails = row;
        $('#view-modal-container').modal('show');
    }

    priceRange() {
        $('#price-range').slider({
            range: true,
            min: 0,
            max: 500,
            values: [
                0, 500
            ],
            slide: (event: any, ui: any) => {
                $('#price-amount').val('$' + ui.values[0] + ' - $' + ui.values[1]);
                this.filterPrice = {
                    minPrice: ui.values[0],
                    maxPrice: ui.values[1]
                }
            }
        });
        $('#price-amount').val('$' + $('#price-range').slider('values', 0) +
            ' - $' + $('#price-range').slider('values', 1));
    }

    // Side Filter
    sideFilter() {

        // let category: any = [];
        // if (this.childCategory && this.childCategory.length) {
        //     this.childCategory.forEach((value: { isChecked: any; _id: any; }) => {
        //         if (value.isChecked) {
        //             category.push({ _id: value._id });
        //         }
        //     });
        // }

        let brand: any = [];
        if (this.brandList && this.brandList.length) {
            brand = this.brandList.filter((value: { isCheckedBrand: any; }) => {
                return value.isCheckedBrand
            });
        }

        let color: any = [];
        if (this.colorsList && this.colorsList.length) {
            color = this.colorsList.filter((value: { isCheckedColor: any; }) => {
                return value.isCheckedColor
            });
        }

        let country: any = [];
        if (this.countryList && this.countryList.length) {
            country = this.countryList.filter((value: { isChecked: any; }) => {
                return value.isChecked;
            });
        }

        // Filter Start
        let filterData: any = []
        if (country && country.length) {
            filterData = this.tmpData.filter((item: any) => {
                for (let i in country) {
                    if (item.ship_from == country[i]._id) {
                        return item;
                    }
                }
            });

            if (color && color.length) {
                filterData = filterData.filter((item: any) => {
                    for (let i in color) {
                        if (item.color_value == color[i].color) {
                            return item;
                        }
                    }
                });
            }

            if (brand && brand.length) {
                filterData = filterData.filter((item: any) => {
                    for (let i in brand) {
                        if (item.brand_name == brand[i].brand) {
                            return item;
                        }
                    }
                });
            }

            if (this.filterPrice && this.filterPrice.minPrice) {
                filterData = filterData.filter((item: any) => {
                    return (item.priceNo > this.filterPrice.minPrice && item.priceNo < this.filterPrice.maxPrice);
                });
            }

        } else if (color && color.length) {
            filterData = this.tmpData.filter((item: any) => {
                for (let i in color) {
                    if (item.color_value == color[i].color) {
                        return item;
                    }
                }
            });

            if (brand && brand.length) {
                filterData = filterData.filter((item: any) => {
                    for (let i in brand) {
                        if (item.brand_name == brand[i].brand) {
                            return item;
                        }
                    }
                });
            }

            if (this.filterPrice && this.filterPrice.minPrice) {
                filterData = filterData.filter((item: any) => {
                    return (item.priceNo > this.filterPrice.minPrice && item.priceNo < this.filterPrice.maxPrice);
                });
            }
        } else if (brand && brand.length) {
            filterData = this.tmpData.filter((item: any) => {
                for (let i in brand) {
                    if (item.brand_name == brand[i].brand) {
                        return item;
                    }
                }
            });

            if (this.filterPrice && this.filterPrice.minPrice) {
                filterData = filterData.filter((item: any) => {
                    return (item.priceNo > this.filterPrice.minPrice && item.priceNo < this.filterPrice.maxPrice);
                });
            }
        } else {
            if (this.filterPrice && this.filterPrice.minPrice) {
                filterData = this.tmpData.filter((item: any) => {
                    return (item.priceNo > this.filterPrice.minPrice && item.priceNo < this.filterPrice.maxPrice);
                });
            }
        }
        this.productList = filterData;

        return true;
        let data = {
            // category: category,
            brand: brand,
            color: color,
            price: this.filterPrice,
            country: country,
            category_id: this.category_id
        }

        this.isSpinner = true;
        this.gs.getBySideFilter(data).subscribe((resp: any) => {
            if (resp.status) {
                this.productList = resp.data;
                for (let i in this.productList) {
                    if (this.productList[i].price && this.productList[i].price['selling_price']) {
                        this.productList[i].priceNo = this.productList[i].price['selling_price'];
                    } else {
                        this.productList[i].priceNo = 0
                    }
                }
                this.tmpData = JSON.parse(JSON.stringify(this.productList));
            }
            this.isSpinner = false;
        }, error => {
            console.log(error);
        });
    }

    // clear filter
    clearFilter() {

        this.getSearchData();

        if (this.childCategory && this.childCategory.length) {
            this.childCategory = this.childCategory.map((isChecked: any) => ({
                ...isChecked,
                isChecked: false,
            }));
        }

        if (this.countryList && this.countryList.length) {
            this.countryList = this.countryList.map((isChecked: any) => ({
                ...isChecked,
                isChecked: false,
            }));
        }

        if (this.brandList && this.brandList.length) {
            this.brandList = this.brandList.map((isCheckedBrand: any) => ({
                ...isCheckedBrand,
                isCheckedBrand: false,
            }));
        }

        if (this.colorsList && this.colorsList.length) {
            this.colorsList = this.colorsList.map((isCheckedColor: any) => ({
                ...isCheckedColor,
                isCheckedColor: false,
            }));
        }
    }

    // Navigate Single product page
    productDetail(Row: any) {
        if (!this.category_id) {
            let cat = JSON.parse(Row['tempcategories']);
            this.category_id = cat[0]['category'];
        }

        if (this.gs.loginData && this.gs.loginData['_id']) {
            this.router.navigate(['/home/single-product', this.category_id, Row._id],
                { queryParamsHandling: this.gs.userTypeDSa ? 'preserve' : null });
        } else {
            this.router.navigate(['/signin'], { queryParams: { wld: this.category_id, proId: Row._id } });
        }
    }

    getSearchData() {
        this.productList = [];
        this.isSpinner = true;
        this.gs.searchTxt = this.serachText;

        let body = {
            text: this.serachText,
            catId: this.category_id,
            create_user: this.gs.loginData['_id'] || '',
        }
        this.gs.searchData(body).subscribe((resp: any) => {
            this.isSpinner = false;

            if (resp.status) {

                this.productList = resp.product;

                // this.breadcrumb.unshift({ name: 'Home' });
                for (let j in resp.brands) {
                    resp.brands[j] = {
                        brand: resp.brands[j]
                    }
                }

                for (let j in resp.colors) {
                    resp.colors[j] = {
                        color: resp.colors[j]
                    }
                }

                this.brandList = resp.brands;
                this.colorsList = resp.colors;
                this.sellerList = resp.sellers;
                this.childCategory = resp.category;


                for (let i in this.productList) {
                    if (this.productList[i].price && this.productList[i].price['selling_price']) {
                        this.productList[i].priceNo = this.productList[i].price['selling_price'];
                    } else {
                        this.productList[i].priceNo = 0
                    }
                }
                this.tmpData = JSON.parse(JSON.stringify(this.productList));
            } else {
                this.toastr.error(resp['message'], 'Oops!', {
                    timeOut: 5000,
                    positionClass: 'toast-top-right',
                    progressBar: true,
                    progressAnimation: 'increasing'
                });
            }
        }, (error: any) => {
            this.isSpinner = false;
        });
    }


    // add To Cart
    addToCart(row: any) {
        // if (this.formObj.collection_product.quantity > row.warehouse_sellable) {
        //     this.toastr.warning("No inventory available.", 'Oops!', {
        //         timeOut: 3000,
        //         positionClass: 'toast-top-right',
        //         progressBar: true,
        //         progressAnimation: 'increasing'
        //     });
        //     return;
        // }

        if (this.gs.loginData['_id']) {
            this.isLoading = true;
            this.formObj.collection_product.quantity = row?.price?.min_quantity || 1;
            this.formObj.collection_product.product_id = row._id;
            this.formObj.collection_product.item_price = row?.price;
            this.formObj.user_collection.collection_type = 'cart';
            this.formObj.user_collection.user_id = this.buyerId ? this.buyerId : this.gs.loginData['_id'];
            this.formObj.user_collection.create_user = this.buyerId ? this.buyerId : this.gs.loginData['_id'];
            this.formObj.collection_product.create_user = this.buyerId ? this.buyerId : this.gs.loginData['_id'];
            this.formObj.user_collection.ship_from = row.ship_from;
            this.formObj.collection_product.user_collection_seller_id = row?.individual;
            this.gs.addToCart(this.formObj).subscribe((resp: any) => {
                this.isLoading = false;
                if (resp.status) {
                    this.gs.cartCount += 1;
                    this.toastr.success('Product added to cart', 'Success', {
                        timeOut: 5000,
                        positionClass: 'toast-top-right',
                        progressBar: true,
                        progressAnimation: 'increasing'
                    });
                    $('#create-cart-quantity').modal('hide');
                    // this.router.navigate(['/home/cart']);
                } else {
                    this.toastr.warning(resp['message'], 'Oops!', {
                        timeOut: 5000,
                        positionClass: 'toast-top-right',
                        progressBar: true,
                        progressAnimation: 'increasing'
                    });
                }
            }, error => {
                this.isLoading = false;
            });
        } else {
            // this.router.navigate(['/signin'], { queryParams: { pd: this.productData._id } });
        }
    }

    // Cart Modal
    openCartModal(row: any) {
        if (this.gs.loginData['_id']) {
            this.formObj.collection_product.quantity = 1
            this.formObj.collection_product.product_id = row._id;
            this.formObj.collection_product.item_price = row?.price;
            $('#create-cart-quantity').modal('show');
        } else {
            this.toastr.warning('Please login to next', 'warning', {
                timeOut: 5000,
                positionClass: 'toast-top-right',
                progressBar: true,
                progressAnimation: 'increasing'
            });
        }
    }


    openWishListModal(row: any) {
        // if (!this.gs.loginData['_id']) {
        //     this.router.navigate(['/signin'], { queryParams: { wld: this.productData._id } });
        //     return;
        // }
        this.isSpinner = true;
        this.productData = row;
        let id = this.buyerId ? this.buyerId : this.gs.loginData['_id'];
        this.gs.getMyWishlist(id).subscribe((resp: any) => {
            this.isSpinner = false;
            this.wishlistItems = resp.data;
            $('#create-wishlist').modal('show');
        }, error => {
            this.isSpinner = false;
        });
    }

    // Add To WishList
    addToWishList(form: any) {
        if (!this.wishlistId) {
            return;
        }

        if (this.gs.loginData['_id']) {
            if (this.formObj.collection_product.quantity >= 1) {
                this.isSpinner = true;
                this.gs.addProductToWL({
                    product_id: this.productData._id,
                    user_collection_id: this.wishlistId,
                    create_user: this.buyerId ? this.buyerId : this.gs.loginData['_id'],
                    product_price: '50',
                    quantity: this.formObj.collection_product.quantity,
                    item_price: this.productData?.price,
                    individual: this.productData.individual,
                    ship_from: this.productData.ship_from,
                }).subscribe((resp: any) => {
                    this.isSpinner = false;

                    if (resp.status) {
                        this.toastr.success('Added to Wishlist', 'Success', {
                            timeOut: 5000,
                            positionClass: 'toast-top-right',
                            progressBar: true,
                            progressAnimation: 'increasing'
                        });
                        $('#create-wishlist').modal('hide');
                        $('#create-new-wishlist').modal('hide');
                        // this.router.navigate(['/my-accounts/wish-list']);
                    } else {
                        this.toastr.error(resp.message, 'warning', {
                            timeOut: 1000,
                            positionClass: 'toast-top-right',
                            progressBar: true,
                            progressAnimation: 'increasing'
                        });
                    }
                });
            } else {
                this.toastr.warning('Please add quantity', 'Warning', {
                    timeOut: 5000,
                    positionClass: 'toast-top-right',
                    progressBar: true,
                    progressAnimation: 'increasing'
                });
            }
        } else {
            this.router.navigate(['/signin']);
        }
    }

    // Filter sort by
    selectFilter(key: any) {
        if (key == '1') {
            this.productList = this.gs.sortByKey(this.productList, 'priceNo');
        }
        if (key == '2') {
            this.productList = this.gs.sortByKeyDesc(this.productList, 'priceNo');
        }
        if (key == '3') {
            this.productList = this.gs.sortByKey(this.productList, 'product_name');
        }
        if (key == '4') {
            this.productList = this.gs.sortByKeyDesc(this.productList, 'product_name');
        }
        if (key == '5') {
            this.productList = this.gs.sortByKey(this.productList, 'total_review');
        }
        if (key == '6') {
            this.sellerList = this.gs.sortByKey(this.sellerList, 'seller_Rating');
            let tempProduct = []
            for (let i in this.sellerList) {
                for (let p in this.productList) {
                    if (this.sellerList[i]._id == this.productList[p].individual) {
                        tempProduct.push(this.productList[p])
                    }
                }
            }
            this.productList = tempProduct
        }
        if (key == '7') {
            this.productList = this.gs.sortByKey(this.productList, 'search_count');
        }
    }

    pageChange() {
        if (typeof window !== 'undefined' && window.document) {
            window.scrollTo({ top: 0, behavior: 'smooth' });
        }
    }

}
