import { Component, OnInit } from '@angular/core';
import { Meta } from '@angular/platform-browser';
import { ActivatedRoute, Router } from '@angular/router';
import { AuthService } from 'src/app/service/auth.service';
import { GlobalService } from 'src/app/service/global.service';
import { environment } from 'src/environments/environment';

@Component({
    selector: 'app-product-category',
    templateUrl: './product-category.component.html',
    styleUrls: ['./product-category.component.scss']
})
export class ProductCategoryComponent implements OnInit {

    isSpinner: boolean = false;
    childCategory: any = [];
    parentData: any = {};
    assetsURL: any = environment.assetsURL
    constructor(public gs: GlobalService,
        private _Activatedroute: ActivatedRoute,
        public auth_service: AuthService,
        private meta: Meta,
        public router: Router) {
        this.gs.updatePageName('Product category');
        this._Activatedroute.params.subscribe(routeParams => {
            this.getChildCategory(routeParams.id);
        });
    }

    ngOnInit(): void {
        this.gs.loadScript();
    }

    // Get Child Category
    getChildCategory(id: any) {
        this.isSpinner = true;
        this.childCategory = [];
        this.parentData = {};
        this.gs.getChildCategoryById(id).subscribe((resp: any) => {
            if (resp.status) {
                this.childCategory = resp.data;
                this.parentData = resp.parentData;
                this.metaTag();
            }
            this.isSpinner = false;
        }, (error: any) => {
            this.isSpinner = false;
            console.log(error);
        });
    }

    // Add Meta Tag
    metaTag() {
        if (this.parentData && this.parentData['seo_meta']) {
            for (let i in this.parentData['seo_meta']) {
                this.meta.addTag({
                    name: this.parentData['seo_meta'][i]['name'],
                    content: this.parentData['seo_meta'][i]['content'],
                    property: this.parentData['seo_meta'][i]['property'],
                });
            }

            if (this.parentData['seo_des']) {
                this.meta.addTag({ name: 'description', content: this.parentData['seo_des'] });
            }
        }

        // Update Page Name
        if (this.parentData && this.parentData['seo_title']) {
            this.gs.updatePageName(this.parentData['seo_title']);
        } else {
            this.gs.updatePageName('Product category');
        }
    }

    // Go To Shop Page
    shop(id: any) {
        if (this.gs.loginData && this.gs.loginData['_id']) {
            this.router.navigate(['/home/shop', id], { queryParamsHandling: 'preserve' });
        } else {
            this.router.navigate(['/signin'], { queryParams: { catId: id } });
        }
    }

}

