import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Lightbox } from 'ngx-lightbox';
import { ToastrService } from 'ngx-toastr';
import { AuthService } from 'src/app/service/auth.service';
import { GlobalService } from 'src/app/service/global.service';
import { environment } from 'src/environments/environment';
import { Location, ViewportScroller } from '@angular/common';
import { SettingService } from 'src/app/service/setting.service';
import { CommonService } from 'src/app/service/common.service';

declare const $: any;

@Component({
    selector: 'app-single-product',
    templateUrl: './single-product.component.html',
    styleUrls: ['./single-product.component.scss']
})
export class SingleProductComponent implements OnInit {

    baseUrl = environment.api2;
    assetsURL = environment.assetsURL

    productData: any = {};
    productSection: any = {};
    isSpinner: boolean = false;
    isDisabled: boolean = false;
    readonly = true;
    breadcrumb: any = [];
    reletedProductList: any = [];
    sellerProductList: any = [];
    sellerProfile: any = {};
    actionPermissions: any = {};
    viewProdDetails: any = {};
    tempWhishListData: any = {};
    addressData: any = [];
    rating: number = 4;
    buyerId: any;
    isReadMore = true;
    isReadMorePopup = true;
    isLoading: boolean = false;

    formObj: any = {
        user_collection: {
            user_id: this.gs.loginData['_id'] || '',
            collection_name: 'cart',
            collection_type: '',
            payment_card_id: '1',
            ship_from: '',
            terms_agree: '',
            status_reason: '',
            delivery_charges: '',
            taxes: '',
            related_business_indv_id: '',
            order_id: null,
            user_collection_seller: [],
            status: 'active',
            editedby: null,
            deleted: null,
            address_id: '',
            create_user: this.gs.loginData['_id'] || '',
            deletedby: null
        },

        collection_product: {
            order_seller_id: '',
            user_collection_id: '',
            product_id: '',
            quantity: 1,
            item_price: {},
            status: 'active',
            order_ret_ref_status: '',
            taxes: 0,
            editedby: null,
            deleted: null,
            create_user: this.gs.loginData['_id'] || '',
            deletedby: null
        }
    }

    wishlistName: any = '';
    wishlistId: any = '';
    currentSlide: any;
    wishlistItems: any = [];
    category_id: any;
    _albums: any = [];

    constructor(public gs: GlobalService,
        private _Activatedroute: ActivatedRoute,
        private toastr: ToastrService,
        public auth_service: AuthService,
        public router: Router,
        public common: CommonService,
        private _location: Location,
        private _lightbox: Lightbox,
        private scroller: ViewportScroller,
        public setService: SettingService,) {
        this.gs.updatePageName('Product Details');
        this._Activatedroute.params.subscribe(routeParams => {
            this.category_id = routeParams.cid
            this.getProduct(routeParams.id, routeParams.cid);
        });

        if (this.gs.loginData && this.gs.loginData['_id']) {
            this.setService.getPermission({ id: this.gs.loginData['_id'] }).subscribe((resp: any) => {
                this.actionPermissions = resp['data'];
            }, (error: any) => {
                console.log(error);
            });
            this._Activatedroute.queryParamMap.subscribe((params) => {
                this.buyerId = params.get('bcid');
            });
        }

    }

    ngOnInit(): void {
        this.getAddress(this.gs.loginData['_id']);
    }

    //slick-carousel Change event
    afterChange(e: any) {
        this.currentSlide = e.currentSlide;
    }

    // Read more
    showText(isTrue: any) {
        if (isTrue) {
            this.isReadMore = !this.isReadMore
        } else {
            this.isReadMorePopup = !this.isReadMorePopup
        }
    }

    // Scroll to details
    scrollToId(id: any) {
        // $("html, body").delay(10).animate({scrollTop: $('#' + id).offset().top }, 2000);
        $('html, body').animate({
            scrollTop: $('#' + id).offset().top - 300
        }, 2000);
    }

    // Get Address 
    getAddress(id: any) {
        this.gs.getAddress(id).subscribe((resp: any) => {
            this.addressData = resp.data;
        }, (error: any) => {
        });
    }

    // Open lightbox image privew
    openLightbox(row: any, imgArrayName: any, index: number): void {
        let finalData = this.auth_service.lightboxImage(row, imgArrayName);
        this._lightbox.open(finalData, index, { wrapAround: true, showImageNumberLabel: true });
    }

    // Close lightbox
    close(): void {
        this._lightbox.close();
    }

    // Quantity
    quantity(quantity: any) {
        if (quantity == 1) {
            this.formObj.collection_product.quantity++
        } else {
            let qt = this.formObj.collection_product.quantity
            if (qt && qt > this.productData.price.min_quantity) {
                this.formObj.collection_product.quantity--
            } else {
                this.toastr.warning('Minimum ' + this.productData.price.min_quantity + ' quantity required', 'Warning', {
                    timeOut: 5000,
                    positionClass: 'toast-top-right',
                    progressBar: true,
                    progressAnimation: 'increasing'
                });
            }
        }
    }

    // Get Product By Id
    getProduct(id: any, cid: any) {
        this.isSpinner = true;

        let data = {
            id: id,
            cid: cid
        }
        this.gs.getProductId(data).subscribe((resp: any) => {
            if (resp.status) {
                this.productData = resp.data;

                if (this.productData.price && this.productData.price.min_quantity) {
                    this.formObj.collection_product.quantity = this.productData.price.min_quantity || 1;
                }

                if(!(this.productData && this.productData.main_image)){
                    this.productData.main_image = '0';
                }

                if(this.productData && this.productData.main_image){
                    let data =  this.productData.images[this.productData.main_image];
                    this.productData.images.splice(this.productData.main_image, 1);
                    this.productData.images.unshift(data);

                    if(this.productData.dthumb_images && this.productData.dthumb_images.length){
                        let thumbData =  this.productData.dthumb_images[this.productData.main_image];
                        this.productData.dthumb_images.splice(this.productData.main_image, 1);
                        this.productData.dthumb_images.unshift(thumbData);
                    }
                }

                this.breadcrumb = resp.scrumb;
                this.productSection = resp;
                this.sellerProfile = resp.sellerProfile;
                
                if (this.productData && this.productData._id) {
                    this.getSellerProduct(this.productData.individual);
                    this.getReletedProduct(this.productData['_id']);
                }

            } else {
                this.toastr.error(resp['message'], 'Oops', {
                    timeOut: 5000,
                    positionClass: 'toast-top-right',
                    progressBar: true,
                    progressAnimation: 'increasing'
                });
                this.router.navigate(['/']);
            }

            this.isSpinner = false;
        }, (error: any) => {
            this.isSpinner = false;
            console.log(error);
        });
    }

    // Releted Product
    getReletedProduct(id: any) {
        this.isSpinner = true;
        this.gs.getReletedProduct({ id: id }).subscribe((resp: any) => {
            this.isSpinner = false;

            if (resp.status) {
                this.reletedProductList = resp.data;
                if (this.reletedProductList && this.reletedProductList.length) {
                    setTimeout(() => {
                        this.slickSlider();
                    }, 0);
                }

            } else {
                this.toastr.error(resp['message'], 'Oops', {
                    timeOut: 5000,
                    positionClass: 'toast-top-right',
                    progressBar: true,
                    progressAnimation: 'increasing'
                });
            }
        }, (error: any) => {
            console.log(error);
            this.isSpinner = false;
        });
    }

    // Ger Seller product
    getSellerProduct(id: any) {
        this.isSpinner = true;
        this.gs.getSellerProduct({ id: id }).subscribe((resp: any) => {
            this.isSpinner = false;
            if (resp.status) {
                this.sellerProductList = resp.data;
                for (let i in this.sellerProductList) {
                    if (this.sellerProductList[i]._id == this.productData._id) {
                        this.sellerProductList.splice(i, 1);
                    }
                }
            } else {
                this.toastr.error(resp['message'], 'Oops', {
                    timeOut: 5000,
                    positionClass: 'toast-top-right',
                    progressBar: true,
                    progressAnimation: 'increasing'
                });
            }
        }, (error: any) => {
            console.log(error);
            this.isSpinner = false;
        });
    }

    // Product Detail
    productDetail(row: any) {
        this.router.navigate(['/home/single-product', this.category_id, row._id]);
    }

    //View product Detail Modal
    viewProductDetail(row: any) {
        this.viewProdDetails = row;
        this.isReadMorePopup = false;
        $('#view-modal-container').modal('show');
    }

    // Add To Cart
    addToCart(productData: any) {
        if(productData.ship_from_code == 'RSWUS'){
            if (this.formObj.collection_product.quantity > productData.warehouse_sellable) {
                this.toastr.warning("This product quantity is available only " + productData.warehouse_sellable, 'Oops!', {
                    timeOut: 3000,
                    positionClass: 'toast-top-right',
                    progressBar: true,
                    progressAnimation: 'increasing'
                });
                return;
            }
        }

        if(productData.ship_from_code == 'RTSIND' || productData.ship_from_code == 'RTSINDEXPRESS'){
            if (this.formObj.collection_product.quantity > productData.seller_inventory) {
                this.toastr.warning("This product quantity is available only " + productData.seller_inventory , 'Oops!', {
                    timeOut: 3000,
                    positionClass: 'toast-top-right',
                    progressBar: true,
                    progressAnimation: 'increasing'
                });
                return;
            }
        }
         
        if (this.gs.loginData['_id']) {
            if (this.formObj.collection_product.quantity >= 1) {
                this.formObj.collection_product.product_id = productData._id;
                this.formObj.collection_product.item_price = productData?.price;
                this.formObj.user_collection.collection_type = 'cart';
                this.formObj.user_collection.user_id = this.buyerId ? this.buyerId : this.gs.loginData['_id'];
                this.formObj.user_collection.create_user = this.buyerId ? this.buyerId : this.gs.loginData['_id'];
                this.formObj.collection_product.create_user = this.buyerId ? this.buyerId : this.gs.loginData['_id'];
                this.formObj.user_collection.ship_from = productData.ship_from;
                this.formObj.collection_product.user_collection_seller_id = productData?.individual;
                
                this.isLoading = true ;
                this.isDisabled = true;
                this.gs.addToCart(this.formObj).subscribe((resp: any) => {
                   this.isDisabled = false;
                   this.isLoading = false ;
                    if (resp.status) {
                        this.gs.cartCount += 1;
                        this.toastr.success('Product added to cart', 'Success', {
                            timeOut: 5000,
                            positionClass: 'toast-top-right',
                            progressBar: true,
                            progressAnimation: 'increasing'
                        });
                        // this.router.navigate(['/home/cart']);
                    } else {
                        this.toastr.warning(resp['message'], 'Oops', {
                            timeOut: 5000,
                            positionClass: 'toast-top-right',
                            progressBar: true,
                            progressAnimation: 'increasing'
                        });
                    }
                });
            } else {
                this.isDisabled = false;
                this.toastr.warning('Please add quantity', 'Warning', {
                    timeOut: 5000,
                    positionClass: 'toast-top-right',
                    progressBar: true,
                    progressAnimation: 'increasing'
                });
            }
        } else {
            this.router.navigate(['/signin'], { queryParams: { pd: productData._id } });
        }
    }

    //Add To Whishlist
    addToWishList(form: any) {
        if (!this.wishlistId) {
            return;
        }
        if (this.gs.loginData['_id']) {
            if (this.formObj.collection_product.quantity >= 1) {
                this.isDisabled = true;
                this.gs.addProductToWL({
                    product_id: this.tempWhishListData._id,
                    user_collection_id: this.wishlistId,
                    create_user: this.buyerId ? this.buyerId : this.gs.loginData['_id'],
                    quantity: this.tempWhishListData?.price?.min_quantity || 1,
                    item_price: this.tempWhishListData?.price,
                    individual: this.productData.individual,
                    ship_from: this.productData.ship_from,
                }).subscribe((resp: any) => {
                    this.isDisabled = false;

                    if (resp.status) {
                        this.toastr.success('Added to Wishlist', 'Success', {
                            timeOut: 5000,
                            positionClass: 'toast-top-right',
                            progressBar: true,
                            progressAnimation: 'increasing'
                        });
                        $('#create-wishlist').modal('hide');
                        $('#create-new-wishlist').modal('hide');
                        // this.router.navigate(['/my-accounts/wish-list']);
                    } else {
                        this.toastr.error(resp.message, 'warning', {
                            timeOut: 1000,
                            positionClass: 'toast-top-right',
                            progressBar: true,
                            progressAnimation: 'increasing'
                        });
                    }
                }, (error: any) => {
                    this.isDisabled = false;
                });
            } else {
                this.toastr.warning('Please add quantity', 'Warning', {
                    timeOut: 5000,
                    positionClass: 'toast-top-right',
                    progressBar: true,
                    progressAnimation: 'increasing'
                });
            }
        } else {
            this.router.navigate(['/signin'], { queryParams: { wld: this.productData._id } });
        }
    }

    //Open Whishlist Popup
    openWishListModal(row: any) {
        if (!this.gs.loginData['_id']) {
            this.router.navigate(['/signin'], { queryParams: { wld: this.productData._id } });
            return;
        }
        this.tempWhishListData = row;
        this.isLoading = true ;
        this.isDisabled = true;
        let id = this.buyerId ? this.buyerId : this.gs.loginData['_id'];
        this.gs.getMyWishlist(id).subscribe((resp: any) => {
            this.isDisabled = false;
            this.isLoading = false;
            this.wishlistItems = resp.data;
            $('#create-wishlist').modal('show');
        }, error => {
            this.isDisabled = false;
        });
    }

    // Update Quantity
    updateQuantity() {
        let qt = this.formObj.collection_product;
        if (!qt.quantity || qt.quantity == 0) {
            this.formObj.collection_product.quantity = this.productData.price.min_quantity || 1;
        } else {
            if (qt && qt.quantity < this.productData.price.min_quantity) {
                this.formObj.collection_product.quantity = this.productData.price.min_quantity || 1;

                this.toastr.warning('Please enter minimum ' + this.productData.price.min_quantity + ' quantity', 'Warning', {
                    timeOut: 5000,
                    positionClass: 'toast-top-right',
                    progressBar: true,
                    progressAnimation: 'increasing'
                });
            }
        }
    }

    //Create New Wishlist
    submitNewWishlist() {
        if (!this.wishlistName) {
            return;
        }
        this.isLoading = true ;
        this.isDisabled = true;

        this.gs.createWishlist({
            name: this.wishlistName,
            create_user: this.gs.loginData['_id']
        }).subscribe((resp: any) => {
            this.isDisabled = false;
           this. isLoading =false ;
            if (resp.status) {
                this.wishlistItems.push(resp.data);
                this.toastr.success('Wishlist created successfully', 'Success', {
                    timeOut: 1000,
                    positionClass: 'toast-top-right',
                    progressBar: true,
                    progressAnimation: 'increasing'
                });
                this.wishlistName = '';
                $('#create-new-wishlist').modal('hide');
            } else {
                this.toastr.error(resp.message, 'warning', {
                    timeOut: 1000,
                    positionClass: 'toast-top-right',
                    progressBar: true,
                    progressAnimation: 'increasing'
                });
            }
        }, (error: any) => {
            this.isDisabled = false;
        });
    }

    // Back To Location
    backClicked() {
        this._location.back();
    }

    // Navigate Page
    navigateShop(id: any) {
        if (this.gs.loginData && this.gs.loginData['_id']) {
            this.router.navigateByUrl('/home', { skipLocationChange: false }).then(() => {
                this.router.navigate(['/seller-product', id]);
            });
        } else {
            this.router.navigate(['/signin'], { queryParams: { catId: id } });
        }
    }

    // Slider Reload
    slickSlider() {
        $('.product-slider').not('.slick-initialized').slick({
            infinite: false,
            arrows: true,
            dots: false,
            slidesToShow: 5,
            slidesToScroll: 1,
            prevArrow: '<button class="slick-prev"><i class="fa fa-angle-left"></i></button>',
            nextArrow: '<button class="slick-next"><i class="fa fa-angle-right"></i></button>',
            responsive: [
                {
                    breakpoint: 1200,
                    settings: {
                        slidesToShow: 3,
                    }
                },
                {
                    breakpoint: 992,
                    settings: {
                        slidesToShow: 2,
                    }
                },
                {
                    breakpoint: 768,
                    settings: {
                        slidesToShow: 2,
                    }
                },
                {
                    breakpoint: 576,
                    settings: {
                        slidesToShow: 1,
                        arrows: false,
                        autoplay: true
                    }
                },
            ]
        });
    }

}
