import { Component, OnInit } from '@angular/core';
import { ToastrService } from 'ngx-toastr';
import { CommonService } from 'src/app/service/common.service';
import { GlobalService } from 'src/app/service/global.service';
import { SettingService } from 'src/app/service/setting.service';


@Component({
  selector: 'app-contactus',
  templateUrl: './contactus.component.html',
  styleUrls: ['./contactus.component.scss']
})
export class ContactusComponent implements OnInit {


  // constructor(public gs: GlobalService) {
  //  this.gs.updatePageName('Contact Us');
  //  }

  // ngOnInit(): void {
  //   this.gs.loadScript();
  // }

    isSpinner: boolean = false;
    emailPattern = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    formObj: any = {
        username: '',
        email: '',
        message: '',
        telephone:'',
        sellerEmail: '',
        key: 'New inquiry buyer'
    };

    constructor(public settingservice: SettingService,
        private toastr: ToastrService,
        private gs: GlobalService,
        ) {
    	this.gs.updatePageName('Contact Us | ExpoBazaar');       
    }

    ngOnInit(): void {
    }

    onSubmit(form: any) {
        if (form.valid) {
            this.isSpinner = true;
            this.formObj.sellerEmail = 'buyers@expobazaar.com';
            this.settingservice.inquiryEmail(this.formObj).subscribe((resp: any) => {
                this.isSpinner = false;
                if (resp['status']) {
                    this.cusThankYouEmail();
                    this.toastr.success(resp['message'], 'Success!');
                } else {
                    this.toastr.error(resp['message'], 'Oops!');
                }
            }, error => {
                this.isSpinner = false;
                console.log(error);
            });
        }
    }

    // custome thank you mail
    cusThankYouEmail() {
        let body = {
            name: this.formObj.username,
            sellerEmail: this.formObj.email,
            key: 'Thank you for buyer inquiry'
        }
        this.settingservice.inquiryEmail(body).subscribe((resp: any) => {
            this.formObj.username = '';
            this.formObj.email = '';
            this.formObj.telephone = '';
            this.formObj.message = '';
        }, error => {
            console.log(error);
        });
    }

}
