import { Component, OnInit } from '@angular/core';
import { CommonService } from 'src/app/service/common.service';
import { GlobalService } from 'src/app/service/global.service';

@Component({
    selector: 'app-shared-value',
    templateUrl: './shared-value.component.html',
    styleUrls: ['./shared-value.component.scss']
})
export class SharedValuesComponent implements OnInit {

    constructor(public gs: GlobalService, private common: CommonService) {
        this.gs.updatePageName('Shared Values | ExpoBazaar');
        this.common.updateSeoTags('Shared Values');
    }

    ngOnInit(): void {
        this.gs.loadScript();
    }

}
