import { Component, OnInit } from '@angular/core';
import { ToastrService } from 'ngx-toastr';
import { environment } from 'src/environments/environment';
import { GlobalService } from 'src/app/service/global.service';


@Component({
  selector: 'app-home-footer',
  templateUrl: './home-footer.component.html',
  styleUrls: ['./home-footer.component.scss']
})
export class HomeFooterComponent implements OnInit {
  appVersion: any = environment.appVersion;
  isSpinner: boolean = false;
  emailadd: any = '';

  constructor(
    public service: GlobalService,
    public toastr: ToastrService,
    ) { }

  ngOnInit(): void {

  }

  subscribeEmail() {
    if (!this.emailadd) {
      this.toastr.error("Please enter email", 'Oops!');
      return;
    }
    this.isSpinner = true;

    this.service.subscribeEmail(this.emailadd).subscribe((resp: any) => {
      this.isSpinner = false;
      if(resp.status){                
          this.toastr.success(resp['message'], 'Success!');
      } else {
          this.toastr.error(resp['message'], 'Oops!');
      }
    }, (error: any) => {
        this.isSpinner = false;
        console.log(error);
    });
  }

}
