import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { ActivatedRoute, NavigationEnd, Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { GlobalService } from 'src/app/service/global.service';
import { environment } from 'src/environments/environment';
declare const $: any;

@Component({
    selector: 'app-seller-product',
    templateUrl: './seller-product.component.html',
    styleUrls: ['./seller-product.component.scss']
})
export class SellerProductComponent implements OnInit {
    baseUrl = environment.api2;
    assetsURL = environment.assetsURL;

    category_id: any = '';
    seller_id: any = '';
    productList: any = [];
    childCategory: any = [];
    isSpinner: boolean = false;
    filterKey: any = '';
    tmpData: any = [];
    readonly = true;
    breadcrumb: any = ['Home'];
    brandList: any = [];
    colorsList: any = [];
    sellerList: any = [];
    isAddingtocart: boolean = false;

    filterObj: any = {
        id: '',
        brand: '',
        color: '',
        seller: ''
    }

    formObj: any = {
        user_collection: {
            user_id: this.gs.loginData['_id'] || '',
            collection_name: 'cart',
            collection_type: '',
            payment_card_id: '1',
            ship_from: '',
            terms_agree: '',
            status_reason: '',
            delivery_charges: '',
            taxes: '',
            related_business_indv_id: '',
            order_id: null,
            user_collection_seller: [],
            status: 'active',
            editedby: null,
            deleted: null,
            address_id: '',
            create_user: this.gs.loginData['_id'] || '',
            deletedby: null
        },

        collection_product: {
            user_collection_id: '',
            product_id: '',
            item_price: {},
            quantity: 1,
            status: 'active',
            order_ret_ref_status: '',
            editedby: null,
            deleted: null,
            create_user: this.gs.loginData['_id'] || '',
            deletedby: null
        }
    }

    constructor(public gs: GlobalService,
        private _Activatedroute: ActivatedRoute,
        private cd: ChangeDetectorRef,
        private toastr: ToastrService,
        private router: Router) {
        this._Activatedroute.paramMap.subscribe(routeParams => {
            this.seller_id = routeParams.get('id');
            this.getProductBySeller(this.seller_id, '', '', '')
        });
        this.gs.updatePageName('Seller Product');
    }

    ngOnInit(): void {

        this.gs.loadScript();
    }

    productDetail(Row: any) {
        this.router.navigate(['/single-product', this.category_id, Row._id]);
    }

    // Get Product By Seller
    getProductBySeller(sellerId: any, brand: any, color: any, id: any) {
        this.productList = [];
        this.isSpinner = true;

        if (!sellerId) {
            sellerId = this.seller_id;
        }

        this.gs.getProductBySeller({
            sellerId: sellerId,
            brand: brand,
            color: color,
            id: id
        }).subscribe((resp: any) => {
            this.breadcrumb = ['Home'];
            this.breadcrumb.push(resp.sellers[0].name);
            this.getChildCategory(this.category_id);
            if (resp.status) {
                this.productList = resp.data;
                this.brandList = resp.brands;
                this.colorsList = resp.colors;
                this.sellerList = resp.sellers;

                for (let i in this.productList) {
                    if (this.productList[i].price && this.productList[i].price['selling_price']) {
                        this.productList[i].priceNo = this.productList[i].price['selling_price'];
                    } else {
                        this.productList[i].priceNo = 0
                    }
                }
                this.tmpData = JSON.parse(JSON.stringify(this.productList));
            }

        }, (error: any) => {
            this.isSpinner = false;
            console.log(error);
        });
    }

    getBySideFilter(id: any, brand: any, color: any, seller: any) {
        this.productList = [];
        this.isSpinner = true;

        this.filterObj.catId = id ? id : this.filterObj.catId ? this.filterObj.catId : this.category_id;
        this.filterObj.brand = brand ? brand : this.filterObj.brand ? this.filterObj.brand : '';
        this.filterObj.color = color ? color : this.filterObj.color ? this.filterObj.color : '';
        this.filterObj.seller = seller ? seller : this.filterObj.seller ? this.filterObj.seller : '';
        this.gs.getBySideFilter(this.filterObj).subscribe((resp: any) => {
            if (resp.status) {
                this.filterKey = '';
                this.productList = resp.data;
                for (let i in this.productList) {
                    if (this.productList[i].price && this.productList[i].price['selling_price']) {
                        this.productList[i].priceNo = this.productList[i].price['selling_price'];
                    } else {
                        this.productList[i].priceNo = 0
                    }
                }
                this.tmpData = JSON.parse(JSON.stringify(this.productList));
            }
            this.isSpinner = false;

        }, (error: any) => {
            this.isSpinner = false;
            console.log(error);
        });
    }

    getChildCategory(id: any) {
        this.gs.getChildCategoryById(id).subscribe((resp: any) => {
            if (resp.status) {
                this.childCategory = resp.data;
            }
            this.isSpinner = false;
        }, (error: any) => {
            this.isSpinner = false;
            console.log(error);
        });
    }

    // add To Cart
    addToCart(row: any) {
        // if (this.formObj.collection_product.quantity > row.warehouse_sellable) {
        //     this.toastr.warning("No inventory available.", 'Oops!', {
        //         timeOut: 3000,
        //         positionClass: 'toast-top-right',
        //         progressBar: true,
        //         progressAnimation: 'increasing'
        //     });
        //     return;
        // }
        
        if (this.gs.loginData['_id']) {
            this.formObj.collection_product.quantity = 1
            this.formObj.collection_product.product_id = row._id;
            this.formObj.collection_product.item_price = row?.price;
            this.formObj.user_collection.collection_type = 'cart';
            this.formObj.user_collection.ship_from = row.ship_from;
            this.formObj.collection_product.user_collection_seller_id = row?.individual;

            this.isAddingtocart = true;
            this.gs.addToCart(this.formObj).subscribe((resp: any) => {
                this.isAddingtocart = false;
                if (resp.status) {
                    this.gs.cartCount += 1;
                    this.toastr.success('Product added to cart', 'Success', {
                        timeOut: 5000,
                        positionClass: 'toast-top-right',
                        progressBar: true,
                        progressAnimation: 'increasing'
                    });
                    // $('#create-cart-quantity').modal('hide');
                    // this.router.navigate(['/home/cart']);
                } else {
                    this.toastr.warning(resp['message'], 'Oops!', {
                        timeOut: 5000,
                        positionClass: 'toast-top-right',
                        progressBar: true,
                        progressAnimation: 'increasing'
                    });
                }
            }, error => {
                this.isAddingtocart = false;
            });

        } else {
            // this.router.navigate(['/signin'], { queryParams: { pd: this.productData._id } });
        }
    }

    // Cart Modal
    // openCartModal(row: any) {
    //     if (this.gs.loginData['_id']) {
    //         this.formObj.collection_product.quantity = 1
    //         this.formObj.collection_product.product_id = row._id;
    //         this.formObj.collection_product.item_price = row?.price;

    //         $('#create-cart-quantity').modal('show');
    //     } else {
    //         this.router.navigate(['/signin'], { queryParams: { pd: row._id } });
    //     }
    // }

    openWishListModal(row: any) {
        $('#create-wishlist').modal('show');
    }

    // Add To WishList
    addToWishList(row: any) {
        if (this.gs.loginData['_id']) {
            this.formObj.collection_product.product_id = row._id;
            this.formObj.user_collection.collection_type = 'wishlist';
            this.gs.addToCart(this.formObj).subscribe((resp: any) => {

                if (resp.status) {
                    this.toastr.success('Added to Wishlist', 'Success', {
                        timeOut: 5000,
                        positionClass: 'toast-top-right',
                        progressBar: true,
                        progressAnimation: 'increasing'
                    });
                    $('#create-wishlist').modal('hide');
                    $('#create-new-wishlist').modal('hide');
                    this.router.navigate(['/my-accounts/wish-list'])
                } else {
                    this.toastr.error(resp['message'], 'Oops!', {
                        timeOut: 5000,
                        positionClass: 'toast-top-right',
                        progressBar: true,
                        progressAnimation: 'increasing'
                    });
                }
            });
        } else {
            this.router.navigate(['/signin'], { queryParams: { pd: row._id } });
        }
    }

    selectFilter(key: any) {
        if (key == '1') {
            this.productList = this.gs.sortByKeyDesc(this.productList, 'product_name');
        }
        if (key == '2') {
            this.productList = this.gs.sortByKeyDesc(this.productList, 'priceNo');
        }
        if (key == '3') {
            this.productList = this.gs.sortByKey(this.productList, 'priceNo');
        }
        if (key == '4') {
            this.productList = this.gs.sortByKey(this.productList, 'createdAt');
        }
        if (key == '5') {
            this.productList = this.gs.sortByKey(this.productList, 'total_review');
        }
        if (key == '6') {
            this.sellerList = this.gs.sortByKey(this.sellerList, 'seller_Rating');
            let tempProduct = []
            for(let i in this.sellerList){
                for(let p in this.productList){
                    if(this.sellerList[i]._id == this.productList[p].individual){
                        tempProduct.push(this.productList[p])
                    }
                }
            }
            this.productList = tempProduct
        }
        if (key == '7') {
            this.productList = this.gs.sortByKey(this.productList, 'search_count');
        }
    }

}
