import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { ToastrService } from 'ngx-toastr';
import { GlobalService } from 'src/app/service/global.service';
import { environment } from 'src/environments/environment';
import { ActivatedRoute, Router } from "@angular/router";
import { DomainvaluesService } from 'src/app/service/domainvalues.service';
import Swal from 'sweetalert2';
import { CommonService } from 'src/app/service/common.service';
import { Lightbox } from 'ngx-lightbox';
import { AuthService } from 'src/app/service/auth.service';
import { SettingService } from 'src/app/service/setting.service';
import { DsaService } from 'src/app/service/dsa.service';

declare const $: any;

@Component({
    selector: 'app-cart',
    templateUrl: './cart.component.html',
    styleUrls: ['./cart.component.scss']
})

export class CartComponent implements OnInit {

    cart_step: any = 1;
    cartData: any = [];
    opetionalVar: any = {};
    assetsURL = environment.assetsURL;
    appEnv = environment.isClient;
    grandTotal: number = 0;
    minDate: any;
    isSpinner: boolean = false;
    isDataLoading: boolean = false;
    isCartDataLoading: boolean = false;
    isLoader: boolean = false;
    checkAddress: any = {};
    checkpaymentMathod: any = {};
    addressData: any = [];
    paymetnMathod: any = [];
    allCountries: any = [];
    allStates: any = [];
    buyerList: any = [];
    sercLocationData: any = [];
    productDetails: any = [];
    alertMassage: boolean = false;
    isReadMore = true
    agree_conditions: boolean = false;
    addrForm: any = {}
    estimateTime: any = {}
    isOPenModal: boolean = false;
    paramsData: any = null;
    userDetails: any = {}
    actionPermissions: any = {}
    placeOrderData: any = {}
    selectd_buyer_id: any = '';
    cartListIndia: any = [];
    cartIndExpress: any = [];
    cartListUsa: any = [];
    currentDate: any = {};
    cntwith: any;
    currentSlide: any;
    shipchargeUSA: any = 0;
    shipchargeIND: any = 0;
    shipchrINDExpress: any = 0;
    totshipCharge: any = 0;

    addressObj: any = {
        address_type: 'CADD',
        add_line1: '',
        add_line2: '',
        city: '',
        state: '',
        zipcode: '',
        status: 'active',
        editedby: null,
        deleted: null,
        code: 'BYRADD',
        deletedby: null,
        individual: '',
        bussiness: '',
        country: ''
    };

    // For Payment method
    ownerDetails: any = {
        cvv: '',
    };
    tempValue: boolean = false;
    typeKey: any;
    isadnewpm: boolean = false;
    newbank: boolean = false;
    isCreating: boolean = false;
    minValue: boolean = false;

    constructor(
        public gs: GlobalService,
        public router: Router,
        public _route: ActivatedRoute,
        private toastr: ToastrService,
        private domainVal: DomainvaluesService,
        public common: CommonService,
        public setService: SettingService,
        private _lightbox: Lightbox,
        private auth_service: AuthService,
        public _service: DsaService,
        private cd: ChangeDetectorRef,

    ) {
        this.gs.updatePageName('Cart');
        if (typeof window !== 'undefined' && window.document) {

            window.addEventListener("message", (ervent) => {
                if (ervent.data && ervent.data.code && ervent.data.code == 105) {

                    let cvv = String(this.ownerDetails.cvv)
                    if (cvv && cvv.length >= 3) {
                        if (!this.isCreating) {
                            this.createPayment(ervent.data);
                        }
                    } else {
                        this.tempValue = true;
                        // this.toastr.error('Please enter cvv', 'Oops!', {
                        //     timeOut: 3000,
                        //     positionClass: 'toast-top-right',
                        //     progressBar: true,
                        //     progressAnimation: 'increasing'
                        // });
                    }
                }
            }, false);
        }

    }

    ngOnInit(): void {
        if (this.gs.loginData && this.gs.loginData['_id']) {
            this.setService.getPermission({ id: this.gs.loginData['_id'] }).subscribe((resp: any) => {
                this.actionPermissions = resp['data'];
            }, (error: any) => {
                console.log(error);
            });
        }
        this.getCountry();
        this.getEstimateTime();

        // Get queryParam Buyer id
        if (this.gs.loginData && this.gs.loginData['_id']) {
            this._route.queryParamMap.subscribe((params) => {
                this.paramsData = params.get('bcid');

                if (this.paramsData != null) {
                    this.getIndividualByID(this.paramsData);
                    this.getcart(this.paramsData);
                    this.getAddress(this.paramsData);
                    this.getPaymentMathod(this.paramsData);
                } else {
                    this.getcart(this.gs.loginData['_id']);
                    this.getAddress(this.gs.loginData['_id']);
                    this.getPaymentMathod(this.gs.loginData['_id']);
                }
            });
        }

    }

    // Payment cvv check
    cvvChange() {
        if (!this.ownerDetails.cvv) {
            this.tempValue = true;
        } else {
            this.tempValue = false;
        }
    }

    // Get Data By individual
    getIndividualByID(id: any) {
        this.auth_service.getIndividualByID({ id }).subscribe((resp: any) => {
            if (resp['status']) {
                this.userDetails = resp.data;
            }
        }, (error: any) => {

        });
    }

    //  Get EstimateTime
    getEstimateTime() {
        this.setService.getCodeDomainVal('BYETDTE').subscribe((resp: any) => {
            if (resp['status']) {
                let estTempArray = resp['data'];

                var today = new Date();
                for (let i in estTempArray) {
                    if (estTempArray[i].code == 'USAESTTIME') {
                        this.estimateTime.usaEstimateTime = estTempArray[i].value;
                        this.currentDate.usaDeliveredBy = new Date(new Date().setDate(today.getDate() + estTempArray[i].absolute_value * 7));
                    }

                    if (estTempArray[i].code == 'INDESTTIME') {
                        this.estimateTime.indiaEstimateTime = estTempArray[i].value;
                        this.currentDate.indiaDeliveredBy = new Date(new Date().setDate(today.getDate() + estTempArray[i].absolute_value * 7));
                    }

                    if (estTempArray[i].code == 'INDEXPRESSTIME') {
                        this.estimateTime.indExpressTime = estTempArray[i].value;
                        this.currentDate.indExpressDeliveredBy = new Date(new Date().setDate(today.getDate() + estTempArray[i].absolute_value * 7));
                    }
                }
            }
        });
    }

    //slick-carousel Change event
    afterChange(e: any) {
        this.currentSlide = e.currentSlide;
    }

    //View product Detail Modal
    viewProductDetail(row: any) {
        this.productDetails = JSON.parse(JSON.stringify(row));

        if (!(this.productDetails && this.productDetails.main_image)) {
            this.productDetails.main_image = '0';
        }
        if (this.productDetails && this.productDetails.main_image) {
            let data = this.productDetails.images[this.productDetails.main_image];
            this.productDetails.images.splice(this.productDetails.main_image, 1);
            this.productDetails.images.unshift(data);

            if (this.productDetails.dthumb_images && this.productDetails.dthumb_images.length) {
                let thumbData = this.productDetails.dthumb_images[this.productDetails.main_image];
                this.productDetails.dthumb_images.splice(this.productDetails.main_image, 1);
                this.productDetails.dthumb_images.unshift(thumbData);
            }
        }
        $('#view-modal-container').modal('show');
    }

    // Go To single product page
    navigateProduct(Row: any) {
        this.router.navigate(['/home/single-product', Row._id, Row._id]);
        $('#view-modal-container').modal('hide');
    }

    // Light Box Preview
    openLightbox(row: any, key: any, index: number): void {
        let finalData = this.auth_service.lightboxImage(row, key);
        this._lightbox.open(finalData, index, { wrapAround: true, showImageNumberLabel: true });
    }

    // Light Box Close
    close(): void {
        this._lightbox.close();
    }

    // read more
    showText() {
        this.isReadMore = !this.isReadMore
    }

    // Get PaymentMathod 
    getPaymentMathod(id: any) {
        this.isDataLoading = true;

        this.gs.getPaymentMathod(id).subscribe((resp: any) => {
            this.paymetnMathod = resp.data;
            this.isDataLoading = false;
        }, (error: any) => {
            this.isDataLoading = false;
        });
    }

    // Get Country
    getCountry() {
        this.domainVal.getAll_Activecountries().subscribe((resp: any) => {
            this.allCountries = resp.data;
            if (this.allCountries && this.allCountries.length) {
                this.allCountries.sort(this.common.dynamicSort("value"));
                const result = this.allCountries.find((x: { value: string; }) => x.value === 'USA');
                if (result && result._id) {
                    this.addressObj.country = result._id
                    this.ChnageCountry(result._id);
                }
            }
        });
    }

    // change country
    ChnageCountry(event: any) {
        let val: any;
        if (event && event.target) {
            val = event.target.value;
        } else {
            val = event;
        }
        this.domainVal.getstateOrCity(val).subscribe((resp: any) => {
            this.allStates = resp.data;
            if (this.allStates && this.allStates.length) {
                this.allStates.sort(this.common.dynamicSort("value"));
            }
        });
    }

    // Get Cart item
    getcart(id: any) {
        this.isCartDataLoading = true;
        this.cartData = [];
        this.cartListUsa = [];
        this.cartListIndia = [];
        this.cartIndExpress = [];
        this.gs.getCartData(id).subscribe((resp: any) => {
            this.isCartDataLoading = false;
            if (resp.status) {
                this.cartData = resp.data;

                for (let i in this.cartData) {
                    // price convert string to number
                    if (this.cartData[i].item_price) {
                        this.cartData[i].item_price.selling_price = Number(this.cartData[i].item_price?.selling_price);
                    }
                    if (this.cartData[i].price) {
                        this.cartData[i].price.selling_price = Number(this.cartData[i].price?.selling_price);
                    }
                    // *** //
                    this.cartData[i].total_price = (this.cartData[i].quantity * this.cartData[i].price.selling_price || 0)

                    if (!(this.cartData[i] && this.cartData[i].main_image)) {
                        this.cartData[i].main_image = "0";
                    }

                    if (this.cartData[i].ship_from_code == 'RSWUS') {
                        this.cartListUsa.push(this.cartData[i]);
                    } else if (this.cartData[i].ship_from_code == 'RTSINDEXPRESS') {
                        this.cartIndExpress.push(this.cartData[i]);
                    } else {
                        this.cartListIndia.push(this.cartData[i]);
                    }


                    this.continueBothCart();
                }

                // this.grandTotal = this.cartData.map((t: any) => Number(t.total_price)).reduce((acc: any, value: any) => acc + value, 0);

                let results = this.cartData.filter((x: any) => x.item_price?.selling_price !== x.price?.selling_price);

                // Price updated Alert msg
                if (results && results.length) {
                    this.alertMassage = true;
                } else {
                    this.alertMassage = false;
                }
            }
        }, (error: any) => {
            this.isCartDataLoading = false;
        });
    }

    // Get Total Global function
    getTotal(array: any) {
        let total = 0;

        if (array && array.length) {
            array.forEach((item: { total_price: any; }) => {
                total += Number(item.total_price);
            });
        }

        return total;
    }

    // Quantity increase and decrease
    cartQuantity(row: any, quantity: number) {
        if (quantity == 1) {
            row.quantity++
        } else {
            if (row && row.quantity > row.item_price.min_quantity) {
                row.quantity--
            } else {
                this.toastr.warning('Minimum ' + row.item_price.min_quantity + ' quantity required', 'Warning', {
                    timeOut: 5000,
                    positionClass: 'toast-top-right',
                    progressBar: true,
                    progressAnimation: 'increasing'
                });
            }
        }

        for (let i in this.cartData) {
            this.cartData[i].total_price = (this.cartData[i].quantity * this.cartData[i].price.selling_price || 0)
        }
        this.grandTotal = this.cartData.map((t: any) => Number(t.total_price)).reduce((acc: any, value: any) => acc + value, 0);

        let data = {
            id: row.user_collection_product_id,
            quantity: row.quantity,
        }

        this.gs.updateQuantity(data).subscribe((resp: any) => { }, (error: any) => {
            console.log(error);
        });


        this.continueBothCart();

        // For Shipping Charge

        if (this.checkAddress && !this.checkAddress._id) {
        } else {
            this.getCharge(this.checkAddress.zipcode);
        }

        // End Shipping Charge
    }

    // Get Address 
    getAddress(id: any) {
        this.gs.getAddress(id).subscribe((resp: any) => {
            this.addressData = resp.data;
        }, (error: any) => {
        });
    }

    // Continue with Both cart button
    continueBothCart() {
        // check price less than available
        if (this.cartListUsa.length && this.cartListIndia.length) {
            if (this.getTotal(this.cartListUsa) < 500 || this.getTotal(this.cartListIndia) < 500) {
                this.minValue = true;
                return;
            } else {
                this.minValue = false;
            }
        }

        if (this.cartIndExpress.length && this.cartListIndia.length) {
            if (this.getTotal(this.cartIndExpress) < 500 || this.getTotal(this.cartListIndia) < 500) {
                this.minValue = true;
                return;
            } else {
                this.minValue = false;
            }
        }

        if (this.cartIndExpress.length && this.cartListUsa.length) {
            if (this.getTotal(this.cartIndExpress) < 500 || this.getTotal(this.cartListUsa) < 500) {
                this.minValue = true;
                return;
            } else {
                this.minValue = false;
            }
        }
    }


    //Get Zone wise Charge
    getCharge(zipcode: any) {

        if (this.cntwith == 'IndiaExpress') {
            return;
        }

        this.isLoader = true;
        let productList = [];
        if (this.cartListUsa && this.cntwith == 'USA') {
            productList = this.cartListUsa;
        } else if (this.cartListIndia && this.cntwith == 'India') {
            productList = this.cartListIndia;
        } else if (this.cartListUsa && this.cartListIndia && this.cntwith == 'both') {
            productList = [...this.cartListUsa, ...this.cartListIndia, ...this.cartIndExpress]
        }
        // else if (this.cartIndExpress && this.cntwith == 'IndiaExpress') {
        //     productList = this.cartIndExpress;
        // } 

        let body = {
            zipcode: zipcode || 0,
            productList: productList
        }

        this.gs.getCharge(body).subscribe((resp: any) => {
            this.shipchargeUSA = resp.shipchargeUSA || 0;
            this.shipchargeIND = resp.shipchargeIND || 0;
            // this.shipchrINDExpress = resp.shipchrINDExpress || 0;
            this.totshipCharge = this.shipchargeUSA + this.shipchargeIND;

            console.log("resp >>>>", resp);

            if (resp.actualCharge) {
                for (let x in this.cartListUsa) {
                    if (resp.actualCharge[this.cartListUsa[x]._id]) {
                        this.cartListUsa[x].shipping_charge = resp.actualCharge[this.cartListUsa[x]._id];
                    }
                }

                for (let x in this.cartListIndia) {
                    if (resp.actualCharge[this.cartListIndia[x]._id]) {
                        this.cartListIndia[x].shipping_charge = resp.actualCharge[this.cartListIndia[x]._id];
                    }
                }
            }

            this.isLoader = false;
        }, (error: any) => {
            this.isLoader = false;
        });
    }

    // Price updated
    priceUpdate(item: any) {
        let data = {
            user_collection_product_id: item.user_collection_product_id,
            product_id: item._id,
            price: item.price
        }
        this.isSpinner = true;
        this.gs.cart_price_update(data).subscribe((resp: any) => {
            this.isSpinner = false;
            if (resp['status']) {
                this.toastr.success('Price has been updated', 'Success', {
                    timeOut: 5000,
                    positionClass: 'toast-top-right',
                    progressBar: true,
                    progressAnimation: 'increasing'
                });
                if (this.paramsData != null) {
                    this.getcart(this.paramsData);
                } else {
                    this.getcart(this.gs.loginData['_id']);
                }
            }

        }, (error: any) => {
            this.isSpinner = false;
            console.log(error);
        });
    }

    // Delete Cart Item
    deleteCartRow(item: any) {
        Swal.fire({
            title: 'Are you sure?',
            text: 'You will not be able to recover this Item!',
            icon: 'warning',
            showCancelButton: true,
            allowOutsideClick: false,
            confirmButtonText: 'Yes, delete it!',
            cancelButtonText: 'No, keep it'
        }).then((result) => {
            if (result.isConfirmed) {
                this.isLoader = true;
                let body = {
                    // user_collection_id: item.user_collection_id,
                    product_id: item._id,
                    user_collection_product_id: item.user_collection_product_id
                }
                this.gs.deleteCartByID(body).subscribe((resp: any) => {
                    this.isLoader = false;
                    this.gs.cartCount -= 1;
                    if (resp.status) {
                        if (this.paramsData != null) {
                            this.getcart(this.paramsData);
                        } else {
                            this.getcart(this.gs.loginData['_id']);
                        }
                    }
                }, (error: any) => {
                    this.isLoader = false;
                });
            }
        });
    }

    // Place order
    async placeOrder() {
        if (this.checkAddress['_id'] && this.checkpaymentMathod['_id']) {
            if (this.agree_conditions) {

                // Check inventery is available
                if (this.cntwith == 'USA' || this.cntwith == 'both') {
                    for (let i in this.cartListUsa) {
                        this.cartListUsa[i].warehouse_sellable = parseInt(this.cartListUsa[i].warehouse_sellable);
                        this.cartListUsa[i].quantity = parseInt(this.cartListUsa[i].quantity);

                        if (this.cartListUsa[i].quantity > this.cartListUsa[i].warehouse_sellable) {
                            this.cartListUsa[i].outOfstock = true;
                            return;
                        } else {
                            this.cartListUsa[i].outOfstock = false;
                        }
                    }
                }

                // Check inventery is Available
                if (this.cntwith == 'India' || this.cntwith == 'both') {
                    for (let i in this.cartListIndia) {
                        this.cartListIndia[i].seller_inventory = parseInt(this.cartListIndia[i].seller_inventory);
                        this.cartListIndia[i].quantity = parseInt(this.cartListIndia[i].quantity);

                        if (this.cartListIndia[i].quantity > this.cartListIndia[i].seller_inventory) {
                            this.cartListIndia[i].outOfstock = true;
                            return;
                        } else {
                            this.cartListIndia[i].outOfstock = false;
                        }
                    }
                }

                // Check inventery is Available
                if (this.cntwith == 'IndiaExpress' || this.cntwith == 'both') {
                    for (let i in this.cartIndExpress) {
                        this.cartIndExpress[i].seller_inventory = parseInt(this.cartIndExpress[i].seller_inventory);
                        this.cartIndExpress[i].quantity = parseInt(this.cartIndExpress[i].quantity);

                        if (this.cartIndExpress[i].quantity > this.cartIndExpress[i].seller_inventory) {
                            this.cartIndExpress[i].outOfstock = true;
                            return;
                        } else {
                            this.cartIndExpress[i].outOfstock = false;
                        }
                    }
                }

                let finalData = [];
                let tempCartList = [];

                if (this.cartListUsa && this.cntwith == 'USA') {
                    tempCartList = this.cartListUsa;
                } else if (this.cartListIndia && this.cntwith == 'India') {
                    tempCartList = this.cartListIndia;
                } else if (this.cartIndExpress && this.cntwith == 'IndiaExpress') {
                    tempCartList = this.cartIndExpress;
                } else if (this.cartListUsa && this.cartListIndia && this.cartIndExpress && this.cntwith == 'both') {
                    tempCartList = [...this.cartListUsa, ...this.cartListIndia, ...this.cartIndExpress]
                }

                for (let i in tempCartList) {
                    finalData.push({
                        _id: tempCartList[i]._id,
                        user_collection_product_id: tempCartList[i].user_collection_product_id,
                        user_collection_seller_id: tempCartList[i].user_collection_seller_id,
                        quantity: parseInt(tempCartList[i].quantity),
                        ship_from: tempCartList[i].ship_from,
                        style_code: tempCartList[i].style_code,
                        product_amount: tempCartList[i].item_price.selling_price,
                        order_amount: tempCartList[i].ship_from_code == 'RSWUS' ? this.getTotal(this.cartListUsa) : tempCartList[i].ship_from_code == 'RTSIND' ? this.getTotal(this.cartListIndia) : this.getTotal(this.cartIndExpress),
                        shipping_charge: tempCartList[i].shipping_charge != undefined ? tempCartList[i].shipping_charge.charge : 0,
                        total_shipping_charge: tempCartList[i].ship_from_code == 'RSWUS' ? this.shipchargeUSA : tempCartList[i].ship_from_code == 'RTSIND' ? this.shipchargeIND : 0,
                    });
                }

                let create_user_id = '';
                if (this.paramsData != null) {
                    create_user_id = this.paramsData;
                } else {
                    create_user_id = this.gs.loginData['_id'];
                }


                let body = {
                    address_id: this.checkAddress['_id'],
                    payment_card_id: this.checkpaymentMathod['_id'],
                    cartData: finalData,
                    create_user: create_user_id,
                    amount: this.getTotal(this.cartListUsa) + this.getTotal(this.cartListIndia) + this.getTotal(this.cartIndExpress),
                    // amount: this.grandTotal,
                    po_number: this.opetionalVar?.po_number || '',
                    po_number2: this.opetionalVar?.po_number2 || '',
                    deliveryNote: this.opetionalVar?.deliveryNote || '',
                    timeSlot: this.opetionalVar?.timeSlot || '',
                    card_cvv: this.checkpaymentMathod.card_cvv,
                    terms_agree: this.agree_conditions,
                    delivery_charges: '',
                    shipping_amount: this.shipchargeUSA + this.shipchargeIND,
                    taxes: '',
                    related_business_indv_id: '',
                }

                this.isSpinner = true;
                this.isLoader = true;
                this.gs.placeOrder(body).subscribe((resp: any) => {
                    this.isSpinner = false;
                    if (resp.status) {
                        this.placeOrderData = resp['data'];
                        this.getCartCount();
                        $('#defaultSize').modal('show');
                    } else {
                        this.toastr.error(resp['message'], 'Oops!', {
                            timeOut: 5000,
                            positionClass: 'toast-top-right',
                            progressBar: true,
                            progressAnimation: 'increasing'
                        });
                    }
                    this.isLoader = false;
                }, (error: any) => {
                    this.isLoader = false;
                    console.log(error);
                    this.isSpinner = false;
                });
            } else {
                this.toastr.error("Please agree to ExpoBazaar's privacy notice and conditions.", 'Oops!', {
                    timeOut: 5000,
                    positionClass: 'toast-top-right',
                    progressBar: true,
                    progressAnimation: 'increasing'
                });
            }
        } else {
            this.toastr.error('Please select address', 'Oops!', {
                timeOut: 5000,
                positionClass: 'toast-top-right',
                progressBar: true,
                progressAnimation: 'increasing'
            });
        }
    }

    // Get Cart Count
    getCartCount() {
        let id: any;
        if (this.paramsData != null) {
            id = this.paramsData;
        } else {
            id = this.gs.loginData['_id'];
        }
        this.common.getCartCount(id);
        this.common.get_notify_count(id);
    }

    // Update Quantity
    updateQuantity(item: any) {

        this.getCharge(this.checkAddress.zipcode);
        if (!item.quantity || item.quantity == 0) {
            item.quantity = item.item_price.min_quantity || 1;
        } else {
            if (item && item.quantity < item.item_price.min_quantity) {
                item.quantity = item.item_price.min_quantity || 1;
                this.cd.detectChanges();
                this.toastr.warning('Please enter minimum ' + item.item_price.min_quantity + ' quantity', 'Warning', {
                    timeOut: 5000,
                    positionClass: 'toast-top-right',
                    progressBar: true,
                    progressAnimation: 'increasing'
                });
            }
        }

        for (let i in this.cartData) {
            this.cartData[i].total_price = (this.cartData[i].quantity * this.cartData[i].price.selling_price || 0)
        }
        this.grandTotal = this.cartData.map((t: any) => Number(t.total_price)).reduce((acc: any, value: any) => acc + value, 0);

        let data = {
            id: item.user_collection_product_id,
            quantity: item.quantity,
        }

        this.continueBothCart();

        this.gs.updateQuantity(data).subscribe((resp: any) => { }, (error: any) => {
            console.log(error);
        });
    }

    // Countinue order
    continueOrder(key: any) {
        if (typeof window !== 'undefined' && window.document) {
            window.scrollTo({ top: 0, behavior: 'smooth' });
        }
        if (key == 3 && !this.checkAddress['_id']) {
            this.toastr.error('Please select address', 'Oops!', {
                timeOut: 5000,
                positionClass: 'toast-top-right',
                progressBar: true,
                progressAnimation: 'increasing'
            });
            return;
        }

        // Check shiping charge available ship from USA
        if (key == 3 && this.shipchargeUSA < 1 && this.cartListUsa.length && (this.cntwith == 'USA' || this.cntwith == 'both')) {
            this.toastr.error('Shipping not available for specified area', 'Oops!', {
                timeOut: 5000,
                positionClass: 'toast-top-right',
                progressBar: true,
                progressAnimation: 'increasing'
            });
            return;
        }

        // Check shiping charge available ship from India
        if (key == 3 && this.shipchargeIND < 1 && this.cartListIndia.length && (this.cntwith == 'India' || this.cntwith == 'both')) {
            this.toastr.error('Shipping not available for specified area', 'Oops!', {
                timeOut: 5000,
                positionClass: 'toast-top-right',
                progressBar: true,
                progressAnimation: 'increasing'
            });
            return;
        }

        if (key == 4 && !this.checkpaymentMathod['_id']) {
            this.toastr.error('Please select payment method', 'Oops!', {
                timeOut: 5000,
                positionClass: 'toast-top-right',
                progressBar: true,
                progressAnimation: 'increasing'
            });
            return;
        }

        if (key == 4 && this.checkpaymentMathod['_id'] && !this.checkpaymentMathod['card_cvv'] ||
            this.checkpaymentMathod['card_cvv'] && this.checkpaymentMathod['card_cvv'].length < 3) {

            this.toastr.error('Please enter cvv', 'Oops!', {
                timeOut: 5000,
                positionClass: 'toast-top-right',
                progressBar: true,
                progressAnimation: 'increasing'
            });
            return;
        }

        if (this.alertMassage) {
            this.toastr.error('Please review your cart carefully', 'Alert!', {
                timeOut: 5000,
                positionClass: 'toast-top-right',
                progressBar: true,
                progressAnimation: 'increasing'
            });
            return;
        }

        this.cart_step = key;
    }

    // Step Change
    async stepChange(step: any, next?: any) {
        if (typeof window !== 'undefined' && window.document) {
            window.scrollTo({ top: 0, behavior: 'smooth' });
        }
        if (step == 1) {
            this.checkAddress = {};
            this.shipchargeUSA = 0;
            this.shipchargeIND = 0;
            // this.shipchrINDExpress = 0;
        }

        if (step == 2 && next) {
            this.cntwith = next;

            // Check inventery is available
            let checkInv = false
            if (this.cntwith == 'USA' || this.cntwith == 'both') {
                for (let i in this.cartListUsa) {
                    this.cartListUsa[i].warehouse_sellable = parseInt(this.cartListUsa[i].warehouse_sellable);
                    this.cartListUsa[i].quantity = parseInt(this.cartListUsa[i].quantity);

                    if (this.cartListUsa[i].quantity > this.cartListUsa[i].warehouse_sellable) {
                        this.cartListUsa[i].outOfstock = true;
                        return;
                    } else {
                        this.cartListUsa[i].outOfstock = false;
                    }
                }

                for (let i in this.cartListUsa) {
                    if (this.cartListUsa[i].outOfstock) {
                        checkInv = true;
                    }
                }
            }

            // Check inventery is Available
            if (this.cntwith == 'India' || this.cntwith == 'both') {
                for (let i in this.cartListIndia) {
                    this.cartListIndia[i].seller_inventory = parseInt(this.cartListIndia[i].seller_inventory);
                    this.cartListIndia[i].quantity = parseInt(this.cartListIndia[i].quantity);

                    if (this.cartListIndia[i].quantity > this.cartListIndia[i].seller_inventory) {
                        this.cartListIndia[i].outOfstock = true;
                        return;
                    } else {
                        this.cartListIndia[i].outOfstock = false;
                    }
                }

                for (let i in this.cartListIndia) {
                    if (this.cartListIndia[i].outOfstock) {
                        checkInv = true;
                    }
                }
            }

            // Check inventery is Available
            if (this.cntwith == 'IndiaExpress' || this.cntwith == 'both') {
                for (let i in this.cartIndExpress) {
                    this.cartIndExpress[i].seller_inventory = parseInt(this.cartIndExpress[i].seller_inventory);
                    this.cartIndExpress[i].quantity = parseInt(this.cartIndExpress[i].quantity);

                    if (this.cartIndExpress[i].quantity > this.cartIndExpress[i].seller_inventory) {
                        this.cartIndExpress[i].outOfstock = true;
                        return;
                    } else {
                        this.cartIndExpress[i].outOfstock = false;
                    }
                }

                for (let i in this.cartIndExpress) {
                    if (this.cartIndExpress[i].outOfstock) {
                        checkInv = true;
                    }
                }
            }

            if (!checkInv) {
                this.cart_step = step;
            }
        }

        if (step != 2 && next) {
            this.cart_step = step;
        } else {
            this.cart_step = step;
        }

    }


    // Add new address
    addNewAddress() {
        $('#add-new-address').modal('show');
    }

    // Address Submit
    submitAddress(form: any) {
        if (form.valid) {
            this.isSpinner = true;

            this.addressObj.individual = this.paramsData ? this.paramsData : this.gs.loginData['_id'];
            this.gs.newAddress(this.addressObj).subscribe((resp: any) => {
                this.isSpinner = false;

                if (resp.status) {
                    this.toastr.success('Address added successfully', 'Success', {
                        timeOut: 5000,
                        positionClass: 'toast-top-right',
                        progressBar: true,
                        progressAnimation: 'increasing'
                    });

                    if (this.paramsData != null) {
                        this.getAddress(this.paramsData);
                    } else {
                        this.getAddress(this.gs.loginData['_id']);
                    }

                    this.clearAddressForm();
                    this.addressData.push(resp.data);

                    $('#add-new-address').modal('hide');
                } else {
                    this.toastr.error(resp['message'], 'Oops!', {
                        timeOut: 5000,
                        positionClass: 'toast-top-right',
                        progressBar: true,
                        progressAnimation: 'increasing'
                    });
                }
            }, (error: any) => {
                console.log(error);
            });
        } else {
            console.log('FormInvalid');
        }
    }

    // Form clear
    clearAddressForm() {
        this.addressObj.id = '';
        this.addressObj.address_type = '';
        this.addressObj.add_line1 = '';
        this.addressObj.add_line2 = '';
        this.addressObj.city = '';
        this.addressObj.state = '';
        this.addressObj.zipcode = '';
        this.addressObj.code = 'CADD';
    }

    // Add new pickup locaiton
    pickupLocation() {
        $('#add-new-plocation').modal('show');
    }

    searchLocation(event: any) {
        if (event.target.value && (event.target.value.length == 6)) {
            this.gs.serchLocation(event.target.value).subscribe((_res) => {
                this.sercLocationData = _res['results'][this.addrForm.zipcode]
            }, (error: any) => {
                console.log(error);
            });
        }
    }

    submitLocation(form: any) {
        $('#add-new-plocation').modal('hide');
        form.reset();
        this.sercLocationData = [];
    }


    // -----------------------------------
    // Payment method
    // -----------------------------------

    addNewCard(key: any) {
        this.typeKey = key;
        this.ownerDetails.ownerName = '';
        this.ownerDetails.ownerStreet = '';
        this.ownerDetails.ownerCity = '';
        this.ownerDetails.ownerState = '';
        this.ownerDetails.ownerZip = '';
        $('#add-new-pmethod').modal('show');
    }

    onSubmitNext(form: any) {
        if (form.valid) {
            this.addNewpm();
            this.isLoader = true;
            setTimeout(() => {
                this.isLoader = false;
            }, 600);
        }
    }

    addNewpm() {

        if (this.typeKey == 1) {
            let chatScript = document.createElement("script");
            chatScript.type = "text/javascript";
            chatScript.src = "https://secure.1stpaygateway.net/secure/paymenthostedform/scripts/firstpay/firstpay.cryptogram.js";
            document.body.appendChild(chatScript);
            this.isadnewpm = true;

        }

        if (this.typeKey == 2) {
            let chatScript = document.createElement("script");
            chatScript.type = "text/javascript";
            chatScript.src = "https://secure-v.goemerchant.com/secure/PaymentHostedForm/Scripts/firstpay/firstpay.cryptogram.js";
            document.body.appendChild(chatScript);
            $('#addbankmodal').modal('show');
            this.newbank = true;
        }
    }

    backPay() {
        this.isadnewpm = false;
    }

    closeCheckout() {
        $('#add-new-pmethod').modal('hide');
        $('#addbankmodal').modal('hide');
        this.newbank = false;
        this.isadnewpm = false;
        this.isCreating = false;

        this.ownerDetails.cvv = '';
    }

    createPayment(data: any) {
        // if (this.isCreating) {
        //     return;
        // }

        this.isCreating = true;

        this.isSpinner = true;
        data["ownerCity"] = this.ownerDetails.ownerCity;
        data["ownerName"] = this.ownerDetails.ownerName;
        data["ownerState"] = this.ownerDetails.ownerState;
        data["ownerStreet"] = this.ownerDetails.ownerStreet;
        data["ownerZip"] = this.ownerDetails.ownerZip;

        if (this.paramsData != null) {
            data['userId'] = this.paramsData;
        } else {
            data['userId'] = this.gs.loginData['_id'];
        }

        this.gs.addPaymentMathod(data).subscribe((resp: any) => {
            this.isSpinner = false;
            this.isCreating = false;

            if (resp.status) {
                this.toastr.success('Payment method added successfully', 'Success', {
                    timeOut: 5000,
                    positionClass: 'toast-top-right',
                    progressBar: true,
                    progressAnimation: 'increasing'
                });

                this.closeCheckout();
                this.paymetnMathod.push(resp.data);
            } else {
                this.toastr.error(resp['message'], "Error", {
                    timeOut: 5000,
                    positionClass: 'toast-top-right',
                    progressBar: true,
                    progressAnimation: 'increasing'
                });
            }
        }, (error: any) => {
            this.isSpinner = false;
            this.isCreating = false;
        });
    }
}
