import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';

import { AuthService } from 'src/app/service/auth.service';
import { CommonService } from 'src/app/service/common.service';
import { DsaService } from 'src/app/service/dsa.service';
import { GlobalService } from 'src/app/service/global.service';
import { SettingService } from 'src/app/service/setting.service';

declare const $: any;


@Component({
   selector: 'app-home-header',
   templateUrl: './home-header.component.html',
   styleUrls: ['./home-header.component.scss']
})
export class HomeHeaderComponent implements OnInit {

   isSpinner: boolean = false;
   allCategory: any = [];
   test: boolean = false;
   formObj = { email: '', password: '' };
   catObj: any;
   categoryId: any;
   searchText: any = '';

   constructor(public auth_service: AuthService,
      public gs: GlobalService,
      public common: CommonService,
      public router: Router,
      private _route: ActivatedRoute,
      private dsaService: DsaService,
      private settings: SettingService,
      private toastr: ToastrService,
   ) {

      if (localStorage.getItem('buyerData') != null) {
         this.gs.loginData = JSON.parse(localStorage.getItem('buyerData') || '')
      }

      this._route.queryParamMap.subscribe((params) => {
         this.gs.globalBuyer = params['params']['bcid'] || null;
         if (this.gs.loginData.userType == 'DSARUSER') {
            this.gs.userTypeDSa = this.gs.loginData.userType;
         }
      });

      this.getAllCate();

   }

   ngOnInit(): void {
        this.gs.loadScript();

/* Offcanvas Menu Start */
      var offCanvasNav = $('.offcanvas-navigation'),
          offCanvasNavSubMenu = offCanvasNav.find('.submenu2');
          
          offCanvasNavSubMenu.slideUp();

      /*Category Sub Menu Toggle*/
      offCanvasNav.on('click', 'li a, li .menu-expand', function (e: any) {

         var $this = $(e.target);
         if (($this.parent().attr('class').match(/\b(menu-item-has-children|has-children|has-sub-menu)\b/)) && ($this.attr('href') === '#' || $this.hasClass('menu-expand'))) {
            e.preventDefault();
            if ($this.siblings('ul:visible').length) {
               $this.parent('li').removeClass('active');
               $this.siblings('ul').slideUp();
            } else {
               $this.parent('li').addClass('active');
               $this.closest('li').siblings('li').removeClass('active').find('li').removeClass('active');
               $this.closest('li').siblings('li').find('ul:visible').slideUp();
               $this.siblings('ul').slideDown();
            }
         }
      });
      /* Offcanvas Menu End */

      this.settings.getMenuPermission({ id: this.gs.loginData._id }).subscribe((res: any) => {
         this.gs.menuPermission = res['data'];
      });

      if (this.gs.loginData['userType'] == 'DSARUSER') {
         this.getGlobalBuyers();
      }
      if (this.gs.loginData && this.gs.loginData['_id']) {
         if (this.gs.loginData && this.gs.loginData['_id']) {
            if (this.gs.globalBuyer && this.gs.globalBuyer != null) {
               this.common.getCartCount(this.gs.globalBuyer);
               this.common.get_notify_count(this.gs.globalBuyer);
            } else {
               this.common.getCartCount(this.gs.loginData['_id']);
               this.common.get_notify_count(this.gs.loginData['_id']);
            }
         }
      }
   }

   getGlobalBuyers() {
      this.dsaService.getManageBuyer({ id: this.gs.loginData['_id'] }).subscribe((resp: any) => {
         this.gs.globalBuyerList = resp.data;
      }, (error: any) => {
         console.log(error);
      });
   }

   gotosearchpage() {
      if (this.catObj) {
         this.categoryId = this.catObj.value
      }
      if (this.gs.searchTxt || this.categoryId) {
         this.router.navigate(['/home/search-result'], {
            queryParams: { 'text': this.gs.searchTxt, 'catid': this.categoryId }
         });
      }
      this.closeOffcanvas();
      // if (this.gs.loginData && this.gs.loginData['_id']) {
      // } else {
      //     this.router.navigate(['/signin'], { queryParams: { catId: this.categoryId } });
      // }
   }

   getAllCate() {
      this.isSpinner = true;
      this.auth_service.getAllCategory().subscribe((resp: any) => {
         this.isSpinner = false;

         if (resp.status) {
            this.allCategory = resp.data;
            this.allCategory.unshift({
               checked: false,
               children: [],
               collapsed: false,
               createdAt: "2021-06-25T05:55:54.947Z",
               deleted: "null",
               name: "All",
               parent: "null",
               status: "active",
               text: "All",
               updatedAt: "2021-11-01T05:10:59.539Z",
               value: "00",
               _id: "00",
            });

            setTimeout(() => {
               $('select').niceSelect('update');
            }, 1000);
         }
      }, (error: any) => {
         this.isSpinner = false;
      });
   }

   logOut() {
      localStorage.clear();
      this.gs.loginData = '';
      this.gs.menuPermission = [];
      this.router.navigateByUrl('/');
      $('#offcanvas-mobile-menu').removeClass('active').addClass('inactive')
   }

   navigateShop(id: any) {
      this.router.navigate(['/home/shop', id], { queryParamsHandling: 'preserve' });

      // if (this.gs.loginData && this.gs.loginData['_id']) {
      //     // this.router.navigateByUrl('/home', { skipLocationChange: false }).then(() => {
      //     // });
      // } else {
      //     this.router.navigate(['/signin'], { queryParams: { catId: id } });
      // }
   }
   closeOffcanvas() {
      $('#offcanvas-mobile-menu').removeClass('active').addClass('inactive')
   }
}

